import React from 'react';
// import './index.css';

const EditEntry = ( props ) => {
	const updateSettings = props.updateSettings;
	const fieldSettings = props.fieldSettings;
	let linkText = '';
	if(typeof fieldSettings.linkText =='undefined'){
		linkText = props.defaultLabel;
	}else{
		linkText =fieldSettings.linkText;
	}

	return (
		<div>
			<div className="pure-control-group">
				<label>Link Text</label>
				<input className="linkText" defaultValue={linkText} onChange={updateSettings} type="text" />
			</div>
		</div>
	)

}

export default EditEntry;