import React from 'react';
// import './index.css';

const DatatableButtons = ( props ) => {
	const updateState = props.updateState;
	const fieldSettings = props.fieldSettings;

	const buttons = [ 'copy', 'csv', 'excel', 'pdf', 'print' ].map( ( ele, index ) => {
		let checked = false;
		if ( typeof fieldSettings.datatableButtons != 'undefined' ) {
			checked = fieldSettings.datatableButtons.includes( ele );
		} else {
			checked = true;
		}
		return <span key={`btn-${ele}`}><input className="datatableButtons" value={ele} type="checkbox" checked={checked} onChange={( e ) => updateState( e, fieldSettings, 'datatableButtons' )} />&nbsp;{ele.toUpperCase()}&nbsp;&nbsp;</span>;
	} )
	return (
		<div>
			<div className="pure-control-group">
				<label>Choose which buttons to display:</label>
				{buttons}
			</div>

		</div>
	)

}

export default DatatableButtons;