import React from 'react';

const DataTablesSettings = ( props ) => {

	const viewSettingsProps = props.viewSettingsProps;

	if ( typeof viewSettingsProps.viewSettings.dataTable == 'undefined' ) {
		viewSettingsProps.viewSettings.dataTable = {
			responsive: false,
			pdfOrientation:'portrait',
			pdfPageSize:'A4'
		};
	}

	return (
		<div>
			<div className="pure-form settings-row">
				<label className="">
					<input name="responsive" type="checkbox" checked={viewSettingsProps.viewSettings.dataTable.responsive || false} onChange={( e ) => viewSettingsProps.saveViewSetting( e, 'dataTable', 'responsive' )} />
					<span>Make DataTable responsive</span>
				</label>
			</div>
			<div className="pure-form settings-row">
				<label className="">
				<span>PDF Orientation</span>
				</label>
				<select onChange={( event ) => viewSettingsProps.saveViewSetting( event, 'dataTable','pdfOrientation' )} value={viewSettingsProps.viewSettings.dataTable.pdfOrientation || 'portrait'}>
					<option value="portrait">Portrait</option>
					<option value="landscape">Landscape</option>
				</select>
			</div>
			<div className="pure-form settings-row">
				<label className="">
					<span>PDF Page Size</span>
				</label>
				<select onChange={( event ) => viewSettingsProps.saveViewSetting( event, 'dataTable','pdfPageSize' )} value={viewSettingsProps.viewSettings.dataTable.pdfPageSize || 'A4'}>
					<option value="A3">A3</option>
					<option value="A4">A4</option>
					<option value="A5">A5</option>
					<option value="LEGAL">LEGAL</option>
					<option value="LETTER">LETTER</option>
					<option value="TABLOID">TABLOID</option>
				</select>
			</div>
		</div>
	)

}

export default DataTablesSettings;