import React from 'react';
import './index.css';

const EditEntrySettings = ( props ) => {

	const viewSettingsProps = props.viewSettingsProps;
	return (
		<div>
			<div className="pure-form edit-settings-row settings-row">
				<label className="">
					<span>Enter Edit Page Link</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
				</label>
				<input className="edit-entry-pagelink" name="editPage" type="text" value={typeof viewSettingsProps.viewSettings.editEntries != 'undefined' ? viewSettingsProps.viewSettings.editEntries.editPage : ''} onChange={( e ) => viewSettingsProps.saveViewSetting( e, 'editEntries', 'editPage' )} />
				<p className="description"> Enter the page link which contains your Form. Users will be redirected to this page to edit entry</p>
			</div>
			<div className="pure-form edit-settings-row settings-row">
				<input name="allowAdminToEdit" type="checkbox" checked={typeof viewSettingsProps.viewSettings.editEntries != 'undefined' ? viewSettingsProps.viewSettings.editEntries.allowAdminToEdit : false} onChange={( e ) => viewSettingsProps.saveViewSetting( e, 'editEntries', 'allowAdminToEdit' )} />
				<span>Allow Administrator to Edit Entries</span>
			</div>
		</div>
	)

}

export default EditEntrySettings;