import React from 'react';
import './index.css';

const FieldsModal = ( props ) => {
	const modalProps = props.modalProps;
	const disallowedField = [ 'password', 'pagebreak', 'divider','hr', 'creditcard', 'page-break' ]
	const fields = Object.keys( modalProps.formFields ).reduce( ( result, fieldId ) => {
		if ( !disallowedField.includes( modalProps.formFields[ fieldId ].fieldType ) ) {
			let fieldInfo = '';
			if ( modalProps.formFields[ fieldId ].fieldType !== 'allFields' ) {
				if ( modalProps.activeSection != 'beforeloop' && modalProps.activeSection != 'afterloop' ) {
					fieldInfo = <span className="description">{`Field Type: ${modalProps.formFields[ fieldId ].fieldType}`} </span>;
				}
				result.push( <li key={`add${fieldId}`} onClick={( event ) => modalProps.addField( event, fieldId )}>
					<div>{modalProps.formFields[ fieldId ].label}</div>
					{fieldInfo}
				</li> )
			} else {
				result.push( <li key={`add${fieldId}`} onClick={( event ) => modalProps.addAllFields( event )}>
				<div>{modalProps.formFields[ fieldId ].label}</div>
				<span className="description">Add all Form Fields to View</span>
			</li> )
			}
		}
		return result;
	}, [] );

	return (
		<ul className="add-fields-list">
			{fields}
		</ul>
	)

}

export default FieldsModal;