import React from 'react';
import { Draggable } from 'react-beautiful-dnd';
import './index.css';

const Field = ( props ) => {
	const fieldProps = props.fieldProps;
	const fieldId = props.fieldId;
	const fieldSettings = props.allFields[fieldId].fieldSettings;
	let label = fieldSettings.useCustomLabel ? fieldSettings.label : props.allFields[fieldId].label;
	if( label === '' ) {
		// if custom label is empty then use the default field label
		label = <div className="hidden-label">{props.allFields[fieldId].label}</div>;
	}
	return (
		<Draggable type={props.sectionName} draggableId={ fieldId } index={ props.index }>
		{
			( provided, snapshot ) => {
				const draggableClasses = snapshot.isDragging? 'activeField' : '';
				return(
					<div className={ `${draggableClasses} field` } { ...provided.draggableProps } { ...provided.dragHandleProps } ref={ provided.innerRef } >
						{ label }
						<span className="close" onClick={ ( event ) => fieldProps.removeField( event, fieldId, props.colId ) }><i className="fas fa-trash-alt"></i></span>
						<span className="settings" onClick={ ( event ) => props.openModal( event, 'fieldSettings', `Field Settings ( ID: ${props.allFields[fieldId].formFieldId})`, fieldId, props.sectionName ) }><i className="fas fa-cog"></i></span>
						{/* <span className="addField" onClick={ ( event ) => props.openModal( event, 'addField', 'Add Field', props.colId, props.sectionName ) } >
							<i className="fas fa-plus"></i>
						</span> */}

					</div>
				)
			}
		}
		</Draggable>
	)
}

export default Field;