import React from 'react';


const SingleEntrySettings = (props) => {
	const viewSettingsProps = props.viewSettingsProps;
	return (
		<div>
			<div className="pure-form edit-settings-row settings-row">
				<label className="">
					<span>Back Link Label</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
				</label>
				<input placeholder="Go back" className="single-entry-backlink-label" name="backLinkLabel" type="text" value={typeof viewSettingsProps.viewSettings.singleEntry != 'undefined' ? viewSettingsProps.viewSettings.singleEntry.backLinkLabel : ''} onChange={( e ) => viewSettingsProps.saveViewSetting( e, 'singleEntry', 'backLinkLabel' )} />
				<p className="description"> Enter the text to display for Go back link on Single Entry Page</p>
			</div>
		</div>
	)

}
export  default SingleEntrySettings;