import React from 'react';


const TabHeading = (props) => {
	let icon = '';
	if(props.tabName === 'multipleentries'){
		icon = <i className="fas fa-copy"></i>;
	}else{
		icon = <i className="fas fa-file"></i>;
	}
	return (
	<li className={`pure-menu-item ${props.activetab=== props.tabName ? 'viewTabActive': ''}`}>

		<span  id = {props.tabName} className = "pure-menu-link" onClick={props.tabClickHandler}>
		{icon}
			{props.label}
		</span>
		</li> );
}

export  default TabHeading;