import React from 'react';
import { isEmpty } from 'lodash';
import {DisabledFilterSortFields} from '../../Constants';
import './index.css';

const SortSettings = (props) => {
	const viewSettingsProps = props.viewSettingsProps;
	const formFields = viewSettingsProps.formFields
	let sortRows = '';
	let sortSettings = [];
	/**
	 * Get Form Fields to show in dropdown
	 */
		const formFieldOptions = Object.keys( formFields ).reduce( ( result, key ) => {
			if ( !DisabledFilterSortFields.includes( formFields[ key ].fieldType ) ) {
				result.push( <option key={`frmfieldoption${key}`} value={formFields[ key ].id}>{formFields[ key ].label}</option> );
			}
			return result;
		}, [] );


	// backward Compatibility
	if( typeof viewSettingsProps.viewSettings.sort.direction === "undefined" ){
		sortSettings = viewSettingsProps.viewSettings.sort;
	}

	/**
	 * Show Add New Filter button if no filter values present
	 */
	if ( isEmpty( sortSettings ) ) {
		sortRows = <button onClick={( event ) => viewSettingsProps.addConditionRow( event, 'sort' )} className="pure-button">Add</button>;
	} else {



		sortRows = sortSettings.map( ( sort, index ) =>{

			return <div key={`sortRow${index}`} className="sortRow">
						<select  onChange={( event ) => viewSettingsProps.saveViewSetting( event, 'sort','field', index )} value={sort.field}>
							{/* <option value="submission_create_date">Submission Create Date</option>
							<option value="submission_update_date">Submission Update Date</option> */}
							{formFieldOptions}
						</select>
						<select  onChange={(event)=> viewSettingsProps.saveViewSetting(event, 'sort','value', index ) } value={sort.value}>
							<option value="ASC">ASC</option>
							<option value="DESC">DESC</option>
						</select>
						<i onClick={( event ) => viewSettingsProps.addConditionRow( event, 'sort')} className="fas fa-plus-circle"></i>
						<i onClick={( event ) => viewSettingsProps.removeConditionRow( event, 'sort', index )} className="fas fa-minus-circle"></i>
					</div>


			// onChange={(e)=> viewSettingsProps.saveViewSetting(e, 'sort') } value={viewSettingsProps.viewSettings.sort.direction}
		});
	}


	return (
			<div className="pure-form sortCont viewInlineSec settings-row">
				<label className="">Sort</label>
				<div className="sortRows">{sortRows}</div>
			</div>

	)

}

export default SortSettings;